import classNames from 'classnames'
import PropTypes from 'prop-types'
import style from './styles.module.css'

const leftModifiers = ['LS', 'LA', 'LC', 'LG']
const rightModifiers = ['RS', 'RA', 'RC', 'RG']

const modifiersMap = {
  LS: { short: 'LSHFT', full: 'Left Shift' },
  LC: { short: 'LCTRL', full: 'Left Control' },
  LA: { short: 'LALT', full: 'Left Alt' },
  LG: { short: 'LGUI', full: 'Left Gui' },
  RS: { short: 'RSHFT', full: 'Left Shift' },
  RC: { short: 'RCTRL', full: 'Left Control' },
  RA: { short: 'RALT', full: 'Left Alt' },
  RG: { short: 'RGUI', full: 'Left Gui' }
}

const noDeselectMessage = ' (This modifier is implicitly used by the active keycode and cannot be de-selected)'

function ModifiersRow ({ selected, implicit, row, onSelect }) {
  return (
    <ul className={style.modifiers}>
      {row.map((mod, i) => (
        <li
          key={i}
          title={modifiersMap[mod].full + (implicit.includes(mod) ? noDeselectMessage : '')}
          className={classNames({
            [style.selected]: selected.includes(mod),
            [style.implicit]: implicit.includes(mod)
          })}
          onClick={() => !implicit.includes(mod) && onSelect(mod)}
        >{modifiersMap[mod].short}</li>
      ))}
    </ul>
  )
}

function ModifiersPicker ({ selected, implicit, onChange }) {
  function handleSelection (modifier) {
    onChange(
      selected.includes(modifier)
        ? selected.filter(mod => mod !== modifier)
        : [...selected, modifier]
    )
  }
  return (
    <>
      <ModifiersRow row={leftModifiers} selected={selected} implicit={implicit} onSelect={handleSelection} />
      <ModifiersRow row={rightModifiers} selected={selected} implicit={implicit} onSelect={handleSelection} />
    </>
  )
}

ModifiersPicker.propTypes = {
  selected: PropTypes.arrayOf(PropTypes.string),
  implicit: PropTypes.arrayOf(PropTypes.string)
}

export default ModifiersPicker
