import PropTypes from 'prop-types'
import { useCallback, useState } from 'react'
import { HslColorPicker } from 'react-colorful'

import ActionButton from '../../Common/ActionButton'

function HSBPicker (props) {
  const { node, onSelect } = props
  const [hsl, setHsl] = useState({
    h: Number(node.params[0].value),
    s: Number(node.params[1].value),
    l: Number(node.params[2].value)
  })

  const handleConfirm = useCallback(() => {
    onSelect([
      { value: Math.round(hsl.h) },
      { value: Math.round(hsl.s) },
      { value: Math.round(hsl.l) }
    ])
  }, [hsl, onSelect])

  return (
    <div>
      <p>Select colour</p>
      <HslColorPicker
        color={hsl}
        onChange={setHsl}
      />
      <p style={{ textAlign: 'center' }}>
        <ActionButton onClick={handleConfirm}>Confirm</ActionButton>
      </p>
    </div>
  )
}

HSBPicker.propTypes = {
  value: PropTypes.any.isRequired,
  prompt: PropTypes.string.isRequired,
  param: PropTypes.oneOfType([
    PropTypes.shape({
      enum: PropTypes.array,
      raw: PropTypes.bool,
      type: PropTypes.string
    })
  ]).isRequired,
  onSelect: PropTypes.func.isRequired
}

HSBPicker.defaultProps = {
  value: ''
}

export default HSBPicker
